import {mapState, mapGetters} from 'vuex';
import {uniIcons, uniPopup} from '@dcloudio/uni-ui';
export default {
  props: {
    // url: {type: String, default: '...'},
    // tempFilePath: {type: String, default: '...'},
  },
  components: {
    uniIcons,
    uniPopup,
  },
  computed: {},
  watch: {},
  data() {
    return {
      task: null,
      progress: 0,
      end: false,
      savedFilePath: '',
    };
  },
  methods: {
    start(url) {
      this.end = false;
      this.progress = 0;
      this.task = uni.downloadFile({
        url,
        success: res => {
          uni.saveFile({
            tempFilePath: res.tempFilePath,
            success: res => {
              this.savedFilePath = res.savedFilePath;
            },
            fail: res => {},
            complete: res => {
              this.end = true;
            },
          });
        },
        fail: res => {
          this.end = true;
        },
      });
      this.task.onProgressUpdate(res => {
        // console.log('下载进度' + res.progress);
        // console.log('已经下载的数据长度' + res.totalBytesWritten);
        // console.log('预期需要下载的数据总长度' + res.totalBytesExpectedToWrite);
        this.progress = res.progress;
      });
      this.$refs.popupDownload.open();
    },
    open() {
      console.log(this.savedFilePath);
    },
    close() {
      this.task.abort();
      this.$refs.popupDownload.close();
    },
  },
};
